.header {
  margin-top: 0.5rem;
  padding-left: 10px;
}

.header ul li {
  list-style: none;
  margin: 0;
  padding: 1.5rem 1rem;
  border-radius: 3px;
}

.header ul li:hover {
  background-color: #444;
}

.header a {
  color: #ffe81f;
}

.header a:hover {
  text-decoration: none;
}

.header h3 {
  margin-top: 1rem;
}

.header h3 a {
  color: white;
}

.header h3 a:hover {
  color: #ffe81f;
}

.header button {
  height: 3.5rem;
  margin-top: 6px;
}
